.App {
  text-align: center;
}


@font-face {
  font-family: 'Rubik';
  src: local('Rubik'), url(./fonts/Rubik-VariableFont_wght.ttf) format('truetype');
}

.App-header {
  background-color: #E5E5E5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  flex-basis: 75%;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.main-text-col {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: calc(100px + 2vmin);
  /* identical to box height */
  color: #5D24FF;

  text-shadow: -10px -10px 30px rgba(93, 36, 255, 0.25), 10px 10px 30px rgba(93, 36, 255, 0.25);
}

.main-text-top-row {
  display:flex;
  flex-basis: 100%;
  width: calc(100px + 60vh);
  flex-direction: row;
  flex-shrink: 0;
}

.main-text-bottom-row {
  display:flex;
  flex-basis: 100%;
  flex-direction: row;
  flex-shrink: 0;
}

.spacer {
  flex-grow: 1;
}